:root {
  --main-blue: #5a5656;
  --second-blue: #008891;
  --third-blue: #00587a;
  /* 00587a #6b6b6b*/
  /* --main-blue: #1d4b4a;
  --second-blue: #54b2af;
  --third-blue: #b1dbd7; */
  --main-grey: #808080;
  --main-black: #1c1c1c;
  --main-white: #f1f1ff;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  background-color: var(--main-white);
  box-sizing: border-box;
}

.mainPage {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  margin-top: 55px;
  background-color: var(--main-white);
}
@media only screen and (hover: none) and (pointer: coarse) {
  .mainPage {
    overflow-y: scroll;
  }
}
